/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-caes-hub-toc-new {
    a {
        color: var(--wp--preset--color--contrast);
    }

    h2,
    h3,
    h4,
    h5,
    h6 {
        font-size: var(--wp--preset--font-size--large);
    }

    ul {
        padding-inline-start: var(--wp--preset--spacing--40);
    }

    ul.list-none {
        padding-inline-start: 0;
        list-style: none;

        ul.list-none {
            padding-inline-start: revert;
        }
    }

    &>*:first-child {
        margin-top: 0;
    }

    &>*:last-child {
        margin-bottom: 0;
    }

    /* Page indicator styles */
    .toc-page-indicator {
        display: inline-block;
        margin-left: 0.5em;
        padding: 0.15em 0.5em;
        font-size: 0.75em;
        font-weight: 600;
        color: #666;
        background-color: #f0f0f0;
        border-radius: 3px;
        white-space: nowrap;
    }

    .toc-other-page > a {
        opacity: 0.65;
    }

    .toc-other-page > a:hover {
        opacity: 1;
    }

    .toc-current-page > a {
        opacity: 1;
    }

    /* Visual divider between pages */
    .toc-other-page:first-of-type {
        margin-top: var(--wp--preset--spacing--40);
        padding-top: var(--wp--preset--spacing--40);
        border-top: 1px solid rgba(0, 0, 0, 0.1);
    }

    &.is-style-caes-hub-style-2 {

        h2,
        h3,
        h4,
        h5,
        h6 {
            font-size: var(--wp--preset--font-size--regular);
            color: var(--wp--preset--color--contrast);
            font-weight: 600;
            margin-bottom: 0;
        }

        li {
            border-left: 1px solid rgba(0, 0, 0, 0.3);
            padding: 0.4rem 0;
            position: relative;

            &.active>a {
                font-weight: bold;
            }

            &.active {
                border-left: 1px solid transparent;

                &::before {
                    content: "âž¤";
                    color: var(--wp--preset--color--bulldog-red);
                    position: absolute;
                    left: -2px;
                    font-size: 16px;
                    position: absolute;
                    top: 3px;
                    left: -5px;
                    width: 16px;
                    height: 16px;
                    padding: var(--space-4) var(--space-0);
                }
            }
        }

        a {
            text-decoration: none;
            padding-left: var(--wp--preset--spacing--40);
            display: block;
            line-height: 1.2;
        }

        /* Page indicator in style-2 */
        .toc-page-indicator {
            background-color: rgba(0, 0, 0, 0.08);
            font-size: 0.7em;
        }

        /* Current page visual indicator */
        .toc-current-page {
            border-left-color: var(--wp--preset--color--bulldog-red);
            border-left-width: 2px;
        }

        .toc-other-page {
            opacity: 0.7;
            
            &:hover {
                opacity: 1;
            }
        }
    }
}

.sticky-toc {
    position: fixed;
    transform: translateX(-200%);
    top: var(--wp--style--block-gap);
    left: 0;
    z-index: 1000;
    transition: transform 0.3s ease-in-out;
    width: 222px;
    max-height: 90vh;
    overflow-y: auto;
    background: var(--wp--preset--color--base);
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
    box-shadow: var(--wp--preset--shadow--small);
    padding: var(--wp--preset--spacing--50);
    line-height: 1.3;
    font-size: var(--wp--preset--font-size--small);
    border: 1px solid var(--wp--preset--color--base-three);
    border-left: 0;

    &.visible {
        transform: translateX(222px);
    }

    h2,
    h3,
    h4,
    h5,
    h6 {
        font-family: var(--wp--preset--font-family--merriweather);
        font-size: var(--wp--preset--font-size--normal);
    }

    a {
        text-decoration: none;
        color: inherit;
    }

    ul,
    ol {
        padding-inline-start: 18px !important;

        li {
            margin: var(--wp--preset--spacing--20) 0;
            position: relative;

            &.active>a {
                font-weight: bold;
            }

            &.active {
                &::before {
                    content: '\279E\00A0\00A0';
                    color: var(--wp--preset--color--bulldog-red);
                    position: absolute;
                    left: -18px;
                }
            }
        }

        padding: 0;

        ul {
            padding-left: var(--wp--preset--spacing--40);
        }
    }

    ul {
        list-style: none;
    }

    ol> {
        li {
            &.active {
                &::before {
                    left: -32px;
                }
            }
        }
    }

    /* Sticky TOC page indicators */
    .toc-page-indicator {
        display: inline-block;
        margin-left: 0.4em;
        padding: 0.15em 0.4em;
        font-size: 0.7em;
        font-weight: 600;
        color: #666;
        background-color: rgba(255, 255, 255, 0.9);
        border-radius: 2px;
        white-space: nowrap;
        border: 1px solid rgba(0, 0, 0, 0.1);
    }

    .toc-other-page > a {
        opacity: 0.65;
    }

    .toc-other-page > a:hover {
        opacity: 1;
    }

    .toc-current-page > a {
        opacity: 1;
    }

    /* Visual divider in sticky TOC */
    .toc-other-page:first-of-type {
        margin-top: var(--wp--preset--spacing--30);
        padding-top: var(--wp--preset--spacing--30);
        border-top: 1px solid rgba(0, 0, 0, 0.1);
    }
}

@media (max-width: 1530px) {
    .sticky-toc {
        display: none;
    }
}

@media print {
    .sticky-toc {
        display: none !important;
        /* Hide on print */
    }
}

.toc-scroll-wrapper {
    max-height: 55vh;
    overflow-y: auto;
    overscroll-behavior: contain;
    padding-right: var(--wp--preset--spacing--40);
}

@media (max-width: 768px) {
    .toc-scroll-wrapper {
        max-height: none;
    }
}

// Print styles
@media print {
    .wp-block-caes-hub-toc-new {
        padding: 0 !important;
        background-color: transparent !important;
        
        /* Hide page indicators in print */
        .toc-page-indicator {
            display: none;
        }
    }
}